    position: absolute;
    right: -120px;
    top: -3px;
    z-index: 9999;
  }

  ::ng-deep .cdk-overlay-pane {
    min-width: initial !important;
    max-width: 96vw !important;
    width: 100% !important;
  }

  table tr td:nth-of-type(4) {
    max-width: 100% !important;
  }

  ::ng-deep .modal-dialog {
    padding: 0 15px;
  }

  .title-card {
    display: none;
  }

  .page-title {
    padding: 0;
    font-size: 17px;
  }

  .m-card h5 {
    display: block;
    /* padding-left: 15%; */
  }

  table {
    width: 100%;
  }

  thead tr {
    position: absolute;
    top: -9999px;
    left: -9999px;
  }

  tr {
    border: none;
  }

  td {
    border: none;
    border-bottom: 1px solid #eee;
    position: relative;
    padding-left: 50%;
  }

  td span.textTtl {
    display: inline-block !important;
    position: absolute;
    top: 50%;
    left: 25px;
    width: 45%;
    padding-right: 10px;
    white-space: nowrap;
    transform: translate(0, -50%);
    font-weight: bold;
    color: #000;
  }

  table tr {
    height: 100%;
    margin-bottom: 25px;
    position: relative;
  }

  table tr {
    border: none;
    margin-bottom: 20px;
    border-radius: 10px;
    width: 100%;
    box-shadow: 0 0 12px 0 rgb(0 0 0 / 19%) !important;
  }

  .example-sidenav-content {
    background: #ececef;
  }

  .mat-table {
    background: transparent;
  }

  tr.mat-header-row {
    height: 0 !important;
  }

  table tr th {
    display: none;
  }

  .btn-toggle:after {
    width: 5rem;
  }

  table tr td {
    width: 100% !important;
    padding-left: 50% !important;
    text-align: left !important;
    height: 45px;
    line-height: 12px;
    padding-top: 15px;
    padding-right: 15px;
    border: none;
  }

  table tr td:last-child {
    padding-top: 5px;
  }

  .example-container tr td:last-child {
    max-width: 100%;
  }

  .mat-table,
  .dashboard .mat-card {
    background: transparent;
  }

  .toprow {
    background: #fff;
    box-shadow: 0 0 12px 0 rgb(0 0 0 / 19%) !important;
    padding-bottom: 0.5rem !important;
    margin-bottom: 15px;
    border-bottom: 2px solid #ccc;
    border-top: 2px solid #ccc;
    padding-left: 20% !important;
  }

  .example-container1 {
    margin-top: 0;
  }

  .dashboard-wrapper {
    padding-left: 0px !important;
    padding-right: 0px !important;
  }

  .norecord {
    background-color: #ffffff !important;
    border-radius: 10px !important;
    box-shadow: 0 0 12px 0 rgba(0, 0, 0, 0.19) !important;
  }

  .norecord tr td {
    width: 100% !important;
    text-align: center;
    border: none;
  }

  .sidefilter .head1 {
    padding-left: 23%;
  }

  .norecord td::before {
    display: none;
  }

  .norecord td {
    padding-left: 0 !important;
  }

  .example-container {
    height: calc(100vh - 335px);
    padding: 0px 6px 0 6px !important;
    overflow-y: scroll;
    overflow-x: hidden;
  }

  .patient-table td.mat-cell:first-of-type,
  .patient-table td.mat-footer-cell:first-of-type {
